
.graph-modal__container {
  padding: 36px 30px 30px;
  max-width: 500px;

  @include mobile {
    padding: 32px 12px 16px;
  }
}
.graph-modal__close {
  top: 20px;
  right: 20px;
  width: 16px;
  height: 16px;

  @include mobile {
    top: 12px;
    right: 12px;
  }
}

.form-modal {

  &__content {
    display: flex;
    flex-direction: column;
    gap: 20px;

    @include tablet {
      gap: 16px;
    }

    @include small-tablet {
      gap: 8px;
    }
  }

  &__title {
    margin-top: 0;
    margin-bottom: 16px;
    font-family: var(--title-family);
    font-weight: 700;
    font-size: 26px;
    line-height: 140%;
    text-align: center;

    @include mobile {
      margin-bottom: 8px;
      font-size: 20px;
      line-height: 120%;
    }

    @include small-mobile {
      font-size: 18px;
    }
  }

  &__descr {
    margin-top: 0;
    margin-bottom: 16px;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
    text-align: center;

    @include mobile {
      margin-bottom: 8px;
      font-size: 14px;
    }
  }

  & .form__btn {
    margin-top: 8px;
    max-width: 280px;
  }
  &  input::placeholder, & textarea::placeholder {
    font-size: 14px;
  }
  &  input, & textarea {
    border: 1px solid var(--dark-text-color);
    border-radius: 10px;
  }

}

.graph-modal__send {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;

  & svg {
    width: 60px;
    height: 60px;
    fill: transparent;
    stroke: #57BA0A;
    stroke-width: 3px;
  }

  & p {
    margin: 0;
    font-family: var(--font-family);
    font-weight: 700;
    font-size: 26px;
    line-height: 140%;
    text-align: center;
  }
  & span {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    line-height: 120%;
  }
}
