:root {
  // base
  --title-family: "AlumniSans", sans-serif;
  --font-family: "PTSans", sans-serif;
  --content-width: 1280px;
  --container-offset: 15px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));

  // colors
  --light-color: #fff; //white
  --dark-text-color: #363636;
  --gray-text-color: #909090;
  --gray-gallery-color: #eaeaea;
  --gray-wild-color: #f5f5f5;
  --blue-color: #2f5894;
  --blue-cronflower-color: #2e63b1;
  --blue-light-color: rgba(86, 165, 255, 0.7);
  --footer-link-color:#00c2ff;
  --orange-color: #fb8b56;
  --gradient-blue: linear-gradient(90deg, #469bff 0%, #153059 100%);
  --gradient-orange: linear-gradient(90deg, #fca880 0%, #fb8b56 100%);
  --red-color:#e10000;
  --green-color:#26966E;
  --yellow-color: #ffd500;
}
