.banner {
  position: relative;
  margin-bottom: 30px;

  @include mobile {
    margin-bottom: 16px;
  }

  &__wrap {
    display: block;
    width: 100vw;
    max-height: 220px;
    overflow: hidden;

    @include tablet {
      max-height: 25vw;
    }
  }

  &__img {
    width: 100%;
    height: 100%;
    min-height: 220px;
    object-fit: cover;
    object-position: center;

    @include tablet {
      min-height: 25vw;
    }
  }
  &__title {
    position: absolute;
    bottom: 0;
    left: 50%;
    border-radius: 10px 10px 0 0;
    padding: 16px;
    width: 50%;
    font-family: var(--title-family);
    text-align: center;
    color: var(--light-color);
    backdrop-filter: blur(5px);
    transform: translateX(-50%);


    @include small-tablet {
      width: 100%;
      white-space: nowrap;
    }
  }
}
