.burger {
  display: none;
  position: relative;
  flex-shrink: 0;
  width: 28px;
  height: 20px;
  padding: 0;
  border: none;
  background-color: transparent;
  outline: none;
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
  transition: transform .5s ease-in-out;
  cursor: pointer;
  z-index: 999;

  & span {
    display: block;
    position: absolute;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: var(--blue-color);
    opacity: 1;
    transform: rotate(0deg);
    transition: .25s ease-in-out;

    &:nth-child(1) {
      top: 0px;
    }
    &:nth-child(2), &:nth-child(3) {
      top: 8px;
      left: 20%;
      right: 0;
      width: 80%;
    }
    &:nth-child(4) {
      top: 16px;
    }
  }

  &.is-open {
    & span {
      &:nth-child(1) {
        top: 8px;
        width: 0%;
        left: 50%;
      }
      &:nth-child(2), &:nth-child(3) {
        left: 0;
        right: 0;
        width: 100%;
      }
      &:nth-child(2) {
        transform: rotate(45deg);
      }
      &:nth-child(3) {
        transform: rotate(-45deg);
      }
      &:nth-child(4) {
        top: 8px;
        width: 0%;
        left: 50%;
      }
    }
  }

  @include tablet {
    display: block;
  }
}
