.product-menu {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 25vw;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 40px;
  padding: 20px 54px;
  width: calc(100% - 25vw);
  height: 100%;
  visibility: hidden;
  transition: opacity .4s ease-in-out, visibility .4s ease-in-out;
  overflow-y: auto;

  &.is-visible {
    opacity: 1;
    visibility: visible;
  }
  &__bg {
    position: fixed;
    top: 0;
    left: 25vw;
    right: 0;
    bottom: 0;
    z-index: -1;
    width: calc(100% - 25vw);
    height: 100%;

    & img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    &::after {
      content: '';
      position: absolute;
      z-index: 0;
      inset: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0, 0, 0, 0.5);
    }
  }



  &__list {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    gap: 8px;
    width: 60%;
    max-width: 570px;
    overflow-y: auto;

    @include tablet {
      position: absolute;
      gap: 0;
      width: 100%;
      transform-origin: top;
      transform: rotateX(90deg);
      opacity: 0;
      visibility: hidden;
      transition: opacity .4s linear, visibility .4s linear, transform .4s linear;

      &.is-open {
        position: relative;
        transform: rotateX(0deg);
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__link {
    display: flex;
    align-items: center;
    gap: 20px;
    border-radius: 10px;
    padding: 12px 20px;
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 0.04em;
    color: var(--light-color);
    text-transform: uppercase;
    background-color: rgba(44, 44, 44, 0.8);
    transition: background-color 0.4s ease-in-out, color 0.4s ease-in-out;
    cursor: pointer;

    @include tablet {
      border-radius: 0;
      padding: 8px 10px;
      font-size: 12px;
      background-color: transparent;
    }

    @include hover {
      color: var(--dark-text-colo);
      background-color: var(--gray-wild-color);

      & img {
        transform: scale(1.1);
      }
    }

    & img {
      flex-shrink: 0;
      width: 164px;
      height: 100px;
      object-fit: contain;
      object-position: center;
      filter: drop-shadow(-5px 5px 4px rgba(0, 0, 0, .4));
      overflow: hidden;
      transition: transform .4s ease-in-out;

      @include tablet {
      width: 114px;
      height: 50px;
      }
    }

    & span {
      font-weight: inherit;
      font-size: inherit;
      letter-spacing:inherit;
      color: inherit;
      text-transform: inherit;
    }
  }

  &__heading {
    position: relative;
    z-index: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 40px;
    margin-left: auto;
    padding-top: 60px;
    width: 40%;
  }
  &__title {
    font-weight: 700;
    font-size: 20px;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    text-align: center;
    color: var(--light-color);
  }

  &__all {
    padding: 12px 52px;

    @include tablet {
      border-bottom: 1px solid var(--light-color);
      border-radius: 0;
      padding-left: 32px;
      width: 100%;
      min-width: 50px;
      font-weight: 500;
      font-size: 12px;
      text-transform: uppercase;
      background-color: transparent;
    }
  }
}
