.page-warehouse {

  &__content {
    display: flex;
    align-items: stretch;
    flex-wrap: wrap;
    gap: 20px;
  }

  &__item {
    width: calc(100%/3 - 20px*2/3);
    height: auto;

    @include small-tablet {
      width: calc(100%/2 - 20px*1/2);
    }
    @include small-mobile {
      width: 100%;
    }
  }

  &__link {}
}
