.footer {
  flex: 0 0 auto;
  padding: 60px 0px 20px;
  color: var(--light-color);
  background: linear-gradient(166deg, #122541 0%, #183f78 100%);

  &__top {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    gap: 60px 50px;
    margin: 0 auto;
    padding: 0 16px;
    max-width: 1400px;

    @include small-desktop {
      flex-wrap: wrap;
      gap: 60px 50px;
    }
    @include tablet {
      gap: 40px 50px;
    }

    @include mobile {
      flex-direction: column;
      align-items: center;
      gap: 30px 20px;
    }
  }

  &__logo {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;

    @include hover {
      & img {
        transform: scale(1.05);
        filter: brightness(1.5);
      }
    }

    & img {
      width: 115px;
      transition: transform .4s ease-in-out, filter .4s ease-in-out;

      @include big-desktop {
        width: 100px;
      }

      @include mobile {
        width: 85px;
      }
    }
  }

  &__contatcs {
    display: flex;
    // align-items: flex-start;
    justify-content: center;
    gap: 41px;

    @include small-desktop {
      order: 1;
      justify-content: space-between;
      width: 100%;
    }

    @include mobile {
      flex-direction: column;
      align-items: center;
      gap: 20px;
    }
  }

  &__department {
    position: relative;
    max-width: 160px;

    @include small-desktop {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      max-width: 100%;
    }

    @include mobile {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      max-width: 100%;
    }

    &:not(:last-child) {
      &::after {
        content: '';
        position: absolute;
        top: 0;
        right: -19.5px;
        width: 1px;
        height: 100%;
        background-color: var(--blue-light-color);

        @include small-desktop {
          display: none;
        }
      }
    }
  }

  &__map {

    @include small-tablet {
      order: 2;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    gap: 40px;
    margin: 0 auto;
    padding: 0 16px;
    padding-top: 40px;
    max-width: 1400px;

    @include tablet {
      flex-direction: column-reverse;
    }
    @include small-tablet {
      gap: 20px;
    }
  }
  &__left {
    display: flex;
    flex-direction: column;
    gap: 4px;
    font-weight: 400;
    font-size: 12px;
    color: var(--light-color);

    @include mobile {
      align-items: center;
    }
  }
  &__copyright {
    font-weight: inherit;
    font-size: inherit;
    color: inherit;
  }
  &__link-policy{
    position: relative;
    font-weight: inherit;
    font-size: inherit;
    color: inherit;
    cursor: pointer;

    &::after {
      content: "";
      position: absolute;
      bottom: -2px;
      left: 50%;
      width: 0;
      height: 0.3px;
      background-color: var(--light-color);
      transition: width 0.4s ease-in-out, left 0.4s ease-in-out;
    }

    @include hover {
      &::after {
        width: 100%;
        left: 0;
      }
    }
  }
}

.footer-department {

  &__title {
    margin-top: 0;
    margin-bottom: 16px;
    font-family: var(--title-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 1;
    letter-spacing: 1px;
    color: var(--light-color);

    @include small-desktop {
      text-align: center;
    }

    @include mobile {
      text-align: center;
      margin-bottom: 8px;
      font-size: 16px;
    }
  }

  &__contact-info {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__link {
    font-weight: 400;
    font-size: 14px;
    color: var(--footer-link-color);
    white-space: nowrap;
    transition: color .4s ease-in-out;

    & svg {
      fill: var(--light-color);
    }
  }
}

.footer-nav {
  display: flex;
  flex-direction: column;
  gap: 8px;

  @include mobile {
    align-items: center;
  }

  &__item {}

  &__link {
    position: relative;
    font-weight: 500;
    font-size: 14px;
    letter-spacing: 0.02em;
    white-space: nowrap;
    text-decoration: none;
    color: var(--light-color);

    &::after {
      content: "";
      position: absolute;
      bottom: -2px;
      left: 50%;
      width: 0;
      height: 0.5px;
      background-color: var(--light-color);
      transition: width 0.4s ease-in-out, left 0.4s ease-in-out;
    }

    @include hover {
      &::after {
        width: 100%;
        left: 0;
      }
    }
  }
}

.map {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  &__img {
    position: relative;
    display: block;
    margin-left: auto;
    width: 100%;
    height: auto;
    max-width: 100%;
    max-height: 200px;

    @include small-tablet {
      max-height: none;
    }
  }


  &__p {
    position: absolute;
    display: inline-block;
    animation-name: floating;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-timing-function: ease-in-out;
    animation-direction: alternate;
    cursor: pointer;

    &::before {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      display: block;
      border-radius: 50%;
      width: 1vw;
      height: 1vw;
      min-width: 16px;
      min-height: 16px;
      background-color: var(--footer-link-color);
      box-shadow: 0 0 0 0 #00c2ffaa;
      transform: scale(1) translateX(-50%);
      cursor: pointer;
      animation: 2s infinite pulse;
    }

    &:focus {
      z-index: 200;

      &::before {
        filter: drop-shadow(0 5px 7px rgba(240, 240, 255, 0.4));
      }

      & .map__text {
        opacity: 1;
        visibility: visible;
      }
    }

    @include hover {
      z-index: 200;

      &::before {
        filter: drop-shadow(0 5px 7px rgba(240, 240, 255, 0.4));
      }

      & .map__text {
        opacity: 1;
        visibility: visible;
      }
    }

    &:active {
      z-index: 200;

      &::before {
        filter: drop-shadow(0 5px 7px rgba(240, 240, 255, 0.4));
      }

      & .map__text {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__p--p1 {
    z-index: 0;
    top: 48%;
    left: 48%;
  }

  &__text {
    position: absolute;
    top: 0;
    right: 0;
    border-radius: 10px;
    padding: 16px;
    min-width: 200px;
    width: fit-content;
    white-space: nowrap;
    color: var(--dark-text-color);
    background-color: var(--light-color);
    opacity: 0;
    visibility: hidden;
    transition: 0.12s ease-in-out;

    @include mobile {
      right: -100px;
      border-radius: 4px;
      padding: 8px;
      padding: 14px;
      min-width: 200px;
      white-space: normal;
    }
  }

  &__p--p2 {
    z-index: 1;
    top: 55%;
    left: 55%;
  }
  @keyframes floating {
    0% {
      transform: translate(0, 0);
      filter: drop-shadow(2px 5px 5px rgba(0, 0, 0, 0.4));
    }

    100% {
      transform: translate(0, 5px);
      filter: drop-shadow(2px 0px 1px rgba(0, 0, 0, 0.4));
    }
  }

  @keyframes pulse {
    0% {
      transform: scale(0.95);
      box-shadow: 0 0 0 0 #00c2ffaa;
    }

    70% {
      transform: scale(1);
      box-shadow: 0 0 0 20px transparent;
    }

    100% {
      transform: scale(0.95);
      box-shadow: 0 0 0 0 transparent;
    }
  }
}

.footer-social {
  display: flex;
  align-items: center;
  gap: 50px;

  @include small-tablet {
    flex-direction: column;
    gap: 20px;
  }

  &__item {
    display: flex;
    align-items: center;
    gap: 16px;

    @include small-tablet {
      flex-direction: column-reverse;
      gap: 8px;
    }
  }

  &__link {
    display: flex;
    align-items: center;
    gap: 8px;
    font-weight: 500;
    font-size: 14px;
    color: var(--footer-link-color);
    transition: color .4s ease-in-out;

    @include hover {
      color: var(--orange-color);
    }

    & img {
      width: 30px;
      height: 30px;
    }

    & span {
      font-weight: inherit;
      font-size: inherit;
      color: inherit;
    }
  }
  &__text {
    font-weight: 400;
    font-size: 14px;
    color: var(--light-color);
  }
}
