.dropdown {
  position: absolute;
  left: 0;
  bottom: 1px;
  display: flex;
  width: 100%;
  height: calc(100vh - var(--header-height));
  color: (--light-color);
  transform: translateY(100%);
  opacity: 0;
  visibility: hidden;
  transition: opacity .4s ease-in-out, visibility .4s ease-in-out;

  .scroll & {
    height: calc(100vh - var(--header-height));
  }

  &.is-visible {
    opacity: 1;
    visibility: visible;
  }

  &__nav {
    width: 25vw;
    background: linear-gradient(172deg, #469bff 0%, #153059 100%);
  }
  &__content {
    width: calc(100% - 25vw);
    background-color: var(--gray-text-color);


  }
}

.dropdown-nav {
  height: 100%;
  overflow: hidden;
  overflow-y: auto;

  &::-webkit-scrollbar {
    width: 8px;
  }

  &::-webkit-scrollbar-track {
    background-color: transparent;
    border-radius: 2px;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 2px;
    background: rgba(44, 44, 44, 0.8);
  }

  .simplebar-scrollbar.simplebar-visible::before {
    border-radius: 2px;
    background-color: rgba(255, 255, 255, 0.8);
    opacity: 1;
  }
  .simplebar-track.simplebar-horizontal {
    border-radius: 2px;
  }
  .simplebar-track.simplebar-vertical {
    border-radius: 2px;
  }

  &__link {
    display: block;
    padding: 20px 23px;
    font-size: 14px;
    text-decoration: none;
    text-align: center;
    color: var(--light-color);
    text-transform: uppercase;
    transition: background-color 0.4s ease-in-out;

    &.is-visible {
      background-color: var(--orange-color);
    }
  }
}
.customize-support .dropdown {
  height: calc(100vh - var(--header-height) - 31px);
}
.customize-support .scroll .dropdown {
  height: calc(100vh - var(--header-height) - 31px);
}
