.page-warehouse-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
  padding: 20px;
  height: 100%;
  background-color: var(--light-color);
  transition: box-shadow .4s ease-in-out;

  @include mobile {
    gap: 8px;
    padding: 8px;
  }

  &:focus {
    outline: none;
    box-shadow: inset 0 4px 4px 0 rgba(248, 252, 255, .25), 0 4px 10px 0 rgba(104, 104, 104, .25);
  }

  @include hover {
    box-shadow: inset 0 4px 4px 0 rgba(248, 252, 255, .25), 0 4px 10px 0 rgba(104, 104, 104, .25);

    & .page-warehouse-card__img img {
      transform: scale(1);
    }
  }

  &:active{
    box-shadow: inset 0 4px 4px 0 rgba(248, 252, 255, .25), 0 4px 10px 0 rgba(104, 104, 104, .25);
  }

  &__img {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;

    & img {
      max-width: 100%;
      object-fit: cover;
      filter: drop-shadow(-5px 5px 4px rgba(0, 0, 0, 0.2));
      transform: scale(0.95);
      transition: transform .4s ease-in-out;
    }
  }

  &__title {
    margin: 0;
    font-family: var(--title-family);
    font-weight: 700;
    font-size: 20px;
    line-height: 1;
    letter-spacing: 1px;
    text-align: center;
    color: var(--dark-text-color);
    transition: color .4s ease-in-out;

    @include small-tablet {
      font-size: 18px;
    }
  }

  &__price {
    margin: 0;
    font-weight: 700;
    font-size: 22px;
    line-height: 137%;
    text-align: center;
    color: var(--blue-color);
  }
}
