.header {
  position: fixed;
  z-index: 50;
  font-size: 0;
  background-color: var(--light-color);
  width: 100%;

  &__top {
    padding: 8px 16px 0;
    width: 100%;

    @include tablet {
      display: none;
    }
    @include small-tablet {
      display: none;
    }

    .scroll & {
      padding-top: 0;
      padding-bottom: 0;
    }
  }

  &__top-container {
    display: flex;
    align-items: center;
    margin: 0 auto;
    max-width: 1400px;
    width: 100%;

    .scroll & {
      position: absolute;
      transform: translateY(-100%);
      opacity: 0;
      display: none;
    }
  }

  &__logo {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    margin-right: 50px;

    @include big-desktop {
      margin-right: 25px;
    }

    @include small-desktop {
      margin-right: 16px;
    }

    @include mobile {
      margin-right: 0;
    }

    @include hover {
      & img {
        transform: scale(1.05);
        filter: brightness(1.5);
      }
    }

    & img {
      width: 115px;
      transition: transform .4s ease-in-out, filter .4s ease-in-out;

      @include big-desktop {
        width: 100px;
      }

      @include tablet {
        width: 80px;
      }

      @include mobile {
        width: 60px;
      }
    }
  }

  &__info {
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 100%;
  }

  &__top-line {
    display: flex;
    align-items: center;
  }

  &__address-container {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px 20px;
    transform: scale(1);
    opacity: 1;
    transition: transform .4s ease-in-out, opacity .4s ease-in-out, position .4s ease-in-out;

    @include small-desktop {
      flex-direction: column;
      align-items: flex-start;
    }
  }

  &__openinghours {
    position: relative;
    margin: 0 20px;
    font-weight: 400;
    font-size: 16px;
    white-space: nowrap;
    color: var(--dark-text-color);
    transform: scale(1);
    opacity: 1;
    transition: transform .4s ease-in-out, opacity .4s ease-in-out, position .4s ease-in-out;

    @include big-desktop {
      margin: 0 16px;
      font-size: 14px;
    }
    @include small-desktop {
      margin: 0 12px;
    }

    &::before {
      content: '';
      display: inline-block;
      margin-right: 4px;
      margin-bottom: 1px;
      width: 6px;
      height: 6px;
      border-radius: 100%;
      background-color: var(--green-color);
      animation: blick 2s linear infinite;
    }

    @keyframes blick {
      0% {
        opacity: 0.3;
      }
      25% {
          opacity: 0.5;
      }
      50% {
          opacity: 1;
      }
      75% {
        opacity: 0.5;
      }
      100% {
          opacity: 0.3;
      }
    }
  }

  &__btn {
    display: flex;
    align-items: center;
    justify-content: center;
    font-weight: 700;
    font-size: 16px;
    text-align: center;
    color: var(--light-color);
    background-image: var(--gradient-orange);
    transition: box-shadow 0.3s ease;

    &:hover {
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    }

    &:not(:last-child) {
      margin-right: 16px;

      @include small-desktop {
        margin-right: 12px;
      }

      @include mobile {
        margin: 0;
      }
    }

    &--blue {
      background-image: var(--gradient-blue);
    }
  }

  &__bottom-line {
    display: flex;
    align-items: center;
  }

  &__form {
    position: relative;
    display: block;
    margin-right: auto;
    transform: scale(1);
    opacity: 1;
    transition: transform .4s ease-in-out, opacity .4s ease-in-out, position .4s ease-in-out;
  }

  &__search-open {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    width: 34px;
    height: 34px;
    color: var(--light-color);
    background-color: #3588e6;
    box-shadow: none;
    transition: box-shadow .4s ease-in-out, background-color .4s ease-in;

    &:focus {
      outline: none;
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    }
    @include hover {
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    }

    & .icon-open {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 22px;
      height: 22px;
      stroke: currentColor;
      stroke-width: 1px;
      fill: currentColor;
      transform: translate(-50%, -50%);
      opacity: 1;
      visibility: visible;
      transition: opacity .4s ease-in-out, visibility .4s ease-in-out;

      .is-visible & {
        opacity: 0;
        visibility: hidden;
      }
    }
    &.is-visible .icon-open {
      opacity: 0;
      visibility: hidden;
    }

    & .icon-close {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 12px;
      height: 12px;
      stroke: currentColor;
      stroke-width: 1px;
      fill: currentColor;
      transform: translate(-50%, -50%);
      opacity: 0;
      visibility: hidden;
      transition: opacity .4s ease-in-out, visibility .4s ease-in-out;
    }
    &.is-visible .icon-close {
      opacity: 1;
      visibility: visible;
    }
  }


  &__social {
    margin-right: 22px;
  }

  &__phones {
    display: flex;
    align-items: center;
    gap: 21px;
  }

  &__link-mail {
    margin-left: auto;
    margin-right: 16px;
    font-weight: 700;
    font-size: 17px;
    text-decoration: none;

    @include small-desktop {
      margin-right: 12px;
    }
  }

  &__link-tel {
    position: relative;
    font-family: var(--title-family);
    font-weight: 700;
    font-size: 28px;

    @include small-desktop {
      font-size: 26px;
    }

    &:not(:last-child) {
      &::after {
        content: '';
        position: absolute;
        top: 0;
        right: calc(-21px/2 - 1px);
        width: 1px;
        height: 100%;
        background-color: var(--blue-light-color);
      }
    }

    & svg {
      margin-right: 4px;
      width: 19px;
      height: 19px;

      @include tablet {
        width: 17px;
        height: 17px;
      }
    }

  }

  &__menu {
    position: relative;
    padding: 16px;
    width: 100%;
    background: linear-gradient(172deg, #469bff 0%, #153059 100%);

    @include tablet {
      display: none;
    }
  }

  &__mobile {
    display: none;

    @include tablet {
      position: relative;
      padding: 4px 16px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      box-shadow: 0 -3px 8px rgba(0, 0, 0, .3);
    }
  }

  &__btns {
    display: flex;
    align-items: center;
    gap: 16px;

    @include small-mobile {
      gap: 8px;
    }
  }

  &__menu-mobile {
    position: absolute;
    top: 100%;
    left: 0;
    overflow-y: auto;
    padding: 16px;
    width: 100%;
    height: calc(100vh - var(--header-height));
    background: linear-gradient(172deg, #469bff 0%, #153059 100%);
    transform-origin: top;
    transform: rotateX(90deg);
    opacity: 0;
    visibility: hidden;
    transition: opacity .4s linear, visibility .4s linear, transform .4s linear;

    &.is-open {
      transform: rotateX(0deg);
      opacity: 1;
      visibility: visible;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    gap: 16px;
    margin: 32px 0;

    & .link-tel, & .link-mail {
      margin: 0;
      color: var(--light-color);

      &::after {
        display: none;
      }

      & span {
        font-weight: inherit;
        font-size: inherit;
        line-height: inherit;
        letter-spacing: inherit;
        color: inherit;
      }

      & svg {
        margin: 0;
        width: 14px;
        height: 14px;
      }
    }

    & .link-tel {
      font-family: var(--title-family);
      font-size: 18px;
      letter-spacing: 0.02em;
    }

    & .link-mail {
      font-size: 14px;
      letter-spacing: 0.05em;
    }

    & .header__openinghours {
      position: relative;
      margin: 0;
      color: var(--light-color);
      transform: scale(1);
      opacity: 1;
    }
  }
}


.header__top-scroll {

  display: none;
  align-items: center;
  margin: 0 auto;
  max-width: 1400px;
  width: 100%;

  .scroll & {
    display: flex;
  }

  & .header__logo {
    margin: 0;
    margin-right: 50px;

    @include small-desktop {
      margin-right: 20px;
    }

    @include mobile {
      margin-right: 0;
    }

    & img {
      width: 60px;
    }
  }
  & .header__contacts--scroll {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 50px;

    @include small-desktop {
      gap: 20px;

      & .header__phones {
        flex-direction: column;
        gap: 0;
      }

      & .header__link-tel:after {
        display: none;
      }
    }

  }
  & .header__social {
    margin-right: 0;
    margin-left: auto;
  }
}



.address {
  display: flex;
  align-items: center;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  color: var(--dark-text-color);

  @include big-desktop {
    font-size: 14px;
  }

		&__icon {
      flex-shrink: 0;
      margin-right: 4px;
      width: 18px;
      height: 19px;
      // width: 15px;
      // height: 16px;
      fill: var(--blue-color);
		}
}

.anim-btn {
  position: relative;
  z-index: 1;
  min-width: 160px;
  min-height: 42px;
  // min-width: 100px;
  // min-height: 32px;

  @include mobile {
    min-width: 90px;
    min-height: 31px;
  }
  @include small-mobile {
    min-width: 83px;
    min-height: 34px;
  }

  &:before {
    content: '';
    position: absolute;
    inset: 0;
    // border-radius: 8px;
    width: 100%;
    height: 100%;
    background: repeating-conic-gradient(from var(--angle), #ffffff 40%, transparent 40%, transparent 70%, #ffffff 90%);
    animation: anim 3s linear infinite;
  }

  &__text {
    position: relative;
    z-index: 2;
    overflow: hidden;
    padding: 10.5px 11px;
    // padding: 5.5px 9px;
    display: flex;
    align-items: center;
    justify-content: center;
    // border-radius: 10px;
    width: calc(100% - 3px);
    height: calc(100% - 3px);
    font-weight: 700;
    font-size: 16px;
    white-space: nowrap;
    text-align: center;
    color: var(--light-color);
    transition: background-color 0.3s ease;

    @include mobile {
      padding: 8px 16px;
      font-size: 12px;
      // text-transform: uppercase;
    }
    @include small-mobile {
      padding: 8px 12px;
      font-size: 12px;
      // text-transform: uppercase;
    }

    .anim-btn & {
      // background-color: var(--orange-color);
      background-image: var(--gradient-orange);
    }
    .header__btn--blue & {
      // background-color: var(--blue-color);
      background-image: var(--gradient-blue);
    }

    &--icon {
      & span {
        font-weight: inherit;
        font-size: inherit;
        text-align: inherit;
        color: inherit;
      }
    }
  }

  &__icon {
    margin-right: 4px;
    width: 14px;
    height: 14px;

    & rect {
      fill: transparent;
    }

    @include mobile {
      width: 12px;
      height: 12px;
    }
  }

}

.search {
  position: absolute;
  top: 50%;
  left: 34px;
  display: flex;
  align-items: center;
  min-width: 400px;
  transform: translateY(-50%);
  opacity: 0;
  visibility: hidden;
  transition: opacity .4s ease-in-out, visibility .4s ease-in-out;

  @include big-desktop {
    min-width: 350px;
  }

  @include small-desktop {
    min-width: 300px;
  }
  &.is-visible {
    opacity: 1;
    visibility: visible;
  }

  &__input {
    margin-left: 8px;
    border-bottom: 1px solid var(--dark-text-color);
    padding: 6px 10px 4px;
    padding-right: 54px;
    width: 100%;
    min-height: 40px;
    outline: none;
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 16px;
    color: var(--dark-text-color);
    background-color: transparent;

    @include small-desktop {
      font-size: 14px;
    }

    &::placeholder {
      font-weight: 400;
      font-size: 16px;
      line-height: 120%;
      color: var(--gray-text-color);

      @include small-desktop {
        font-size: 14px;
      }
    }

    &:-webkit-autofill,
    &:-webkit-autofill:hover,
    &:-webkit-autofill:focus {
      -webkit-box-shadow: 0 0 0px 40rem #ffffff inset;
    }
  }

  &__btn {
    position: absolute;
    right: 16px;
    top: 50%;
    border: none;
    font-size: 0;
    color: var(--gray-text-color);
    background: none;
    cursor: pointer;
    transform: translateY(-50%);

    @include hover {
      color: var(--orange-color);
    }

    & svg {
      width: 22px;
      height: 22px;
      fill: currentColor;
    }

  }
}

.social {
  display: flex;
  align-items: center;
  gap: 16px;

  &__item {
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 100%;
    width: 34px;
    height: 34px;
    color: var(--light-color);
    transition: transform .4s ease-in-out, box-shadow .4s ease-in-out;

    @include hover {
      box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
    }

    &--tg {
      background-color: #039be5;

    }
    &--yt {
      background-color: #ff0000;
    }
    &--vb {
      background-color: #665cac;
    }

  }

  &__icon {
    width: 20px;
    height: 20px;
    fill: currentColor;
    transform: none;
  }
}

.menu {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 0 auto;
  width: 100%;
  max-width: 1400px;

  &__item {}

  &__link {
    display: inline-block;
    position: relative;
    font-size: 14px;
    text-decoration: none;
    letter-spacing: 0.04em;
    color: var(--light-color);
    white-space: nowrap;
    text-transform: uppercase;

    @include tablet {
      font-size: 13px;
    }

    &::after {
      content: "";
      position: absolute;
      bottom: -2px;
      left: 50%;
      width: 0;
      height: 0.5px;
      background-color: var(--light-color);
      transition: width 0.4s ease-in-out, left 0.4s ease-in-out;
    }

    @include hover {
      &::after {
        width: 100%;
        left: 0;
      }
    }
    &.is-visible {
      &::after {
        width: 100%;
        left: 0;
      }
    }
  }
}

.menu-mobile {

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    gap: 16px;
    padding: 16px 0;
    font-size: 14px;
    text-decoration: none;
    color: var(--light-color);
    text-transform: uppercase;
    overflow-y: auto;

    & svg {
      width: 12px;
      height: 12px;
      fill: transparent;
      stroke: currentColor;
      stroke-width: 2px;
      transition: transform .4s ease-in-out;
    }

    &.is-open {
      & svg {
        transform: rotate(180deg);
      }
    }

  }
}

.submenu {
  position: absolute;
  transform-origin: top;
  transform: rotateX(90deg);
  opacity: 0;
  visibility: hidden;
  transition: opacity .4s linear, visibility .4s linear, transform .4s linear;

  &.is-open {
    position: relative;
    transform: rotateX(0deg);
    opacity: 1;
    visibility: visible;
  }

  &__link {
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-bottom: 1px solid var(--light-color);
    padding: 16px;
    width: 100%;
    font-size: 14px;
    text-align: left;
    text-decoration: none;
    color: var(--light-color);
    text-transform: uppercase;
    overflow-y: auto;

    & svg {
      width: 12px;
      height: 12px;
      fill: transparent;
      stroke: currentColor;
      stroke-width: 2px;
      transition: transform .4s ease-in-out;
    }

    &.is-open{
      & svg {
        transform: rotate(180deg);
      }
    }
  }
}
