.breadcrumbs {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding-bottom: 60px;

  @include mobile {
    padding-bottom: 30px;
  }

  &__item {
    display: flex;
    font-weight: 400;
    font-size: 16px;
    line-height: 137%;
    color: var(--gray-text-color);

    @include mobile {
      font-size: 14px;
    }

    &--current {
      color: var(--dark-text-color);
    }

    &:not(:last-child) {
      &::after {
        content: '';
        display: inline-block;
        margin: 0 15px;
        width: 6px;
        background-image: url('../img/separator.svg');
        background-repeat: no-repeat;
        background-position: center;
        background-size: contain;

        @include mobile {
          margin: 0 7px;
        }
      }
    }
  }

  &__link {
    position: relative;
    display: flex;
    align-items: center;
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit;
    color: inherit;
    transition: color .5s ease-in-out;

    &::after {
      content: "";
      position: absolute;
      bottom: 1px;
      left: 50%;
      width: 0;
      height: 0.75px;
      background-color: currentColor;
      transition: width .5s ease, left .5s ease;
  }

    @include hover {
      color: var(--orange-color);
      &::after {
        width: 100%;
        left: 0;
      }
    }

    & span {
      font-weight: inherit;
      font-size: inherit;
      line-height: inherit;
      color: inherit;
    }
  }

  & span {
    font-weight: inherit;
    font-size: inherit;
    line-height: inherit;
    color: inherit;
  }

}
